@import "../../styles/communs.scss";


.containerProTrust {
  margin-top:50px;
  margin-bottom:50px;
  height: auto;
  display: flex;
  flex-direction: column;
}

.containerNamePro {
 
  display: flex;
  justify-content: space-around;
  gap: 5px;
  padding-top: 70px;
  flex-wrap: wrap;
  width: 100%;
}

.logoPro {
  width: 200px;
  height: $heightPro;
  object-fit: contain;
  // outline: 1px solid black;
}


.trust {
  background: white;
  cursor: pointer;
  border-radius: 15px;
  position: relative;
  padding: 25px 30px;
  color: var($colorOrange2);
  height: 200px;
  width: 190px;
  outline: 1px solid $colorOrange2;
}

.trust::after {
  content: "";
  background: $colorOrange2;
  border-radius: 15px;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 0;
  clip-path: circle(10% at 0% 0%);
  transition: all 0.3s ease-in;
}

.content {
  position: relative;
  z-index: 1;
  transition: all 0.3s ease-in;
}

.trust:hover::after {
  clip-path: circle(100%);
}

.trust:hover .content {
  color: $colorWhite;
}

@media #{$medium-down} {
   
  .containerNamePro {
         padding-top: 10px;
    }
  }
      